.page-footer {
  padding: 50px 0;

  background-color: $color-tangaroa;

  @include vp-1279 {
    padding: 50px 0;
  }
  @include vp-767 {
    padding: 40px 0;
  }
}

.page-footer__wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  @include vp-767 {
    flex-direction: column;
    align-items: flex-start;
  }
}

.page-footer__logo-link {
  flex-shrink: 0;
  margin-right: 40px;

  @include vp-1279 {
    margin-right: 25px;
  }
  @include vp-767 {
    margin-right: 0;
    margin-bottom: 20px;
  }
}

.page-footer__nav {
  justify-content: flex-start;
}

.page-footer__wrapper-social {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;

  @include vp-767 {
    margin-left: 0;
  }
}

.page-footer__wrapper-social a {
  display: inline-block;
  margin-right: 33px;

  vertical-align: baseline;

  @include vp-1279 {
    margin-right: 23px;
  }

  &:last-of-type {
    margin-right: 0;
  }

  &:hover,
  &:focus {
    opacity: 0.8;
  }
}
